.route-container{
    display: grid;
    grid-template-columns: 20em 20em;
    grid-auto-rows: minmax(0px, auto);
    grid-row-gap: 3em;
    grid-column-gap: 5em;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
    /* padding: 0px 5em; */
}
.route-item{
    height: 16em;
    /* border: 1px solid #000; */
    background-color: #fff;
    -webkit-box-shadow: 0px 2px 2px #d7d1bd;
            box-shadow: 0px 2px 2px #d7d1bd;
    cursor: pointer;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}
.route-item:hover{
    -webkit-box-shadow: 0px 4px 4px #d7d1bd;
            box-shadow: 0px 4px 4px #d7d1bd;
    -webkit-transform: scale(1.1, 1.1);
        -ms-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}

.route-item-child2{
    color: #2f3640;
}

.route-item-child1{
    height: 60%;
}

.bus_name{
    padding: 0;
    text-align: center;
    margin: 15px 0 20px 0;
}
.info-div{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.info-div>.info-div-child1, .info-div>.info-div-child2{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-left: 15px;
    font-size: 10px;
    color: #718093;
}

.info-div-child1 > span, .info-div-child2 > span{
    margin-left: 5px;
}

@media only screen and (max-width: 1000px){
    .route-container{
        grid-column-gap: 3em ;
        /* padding: 0px 3em; */
    }
    /* div.jado{
        border: 5px solid #000;
    } */
}

@media only screen and (max-width: 970px){
    .route-container{
        grid-template-columns: 15em 15em !important;
    }
}

@media only screen and (max-width: 700px){
    .route-container{
        grid-template-columns: 15em !important;
    }
}

.add-btn-child{
    padding-left: 24px;
    padding-right: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
    outline: none;
    margin: 0px;
    background: rgb(111, 255, 176);
    border-radius: 96px;
    width: 7rem !important;
    cursor: pointer;
    color: #444444;
}
.add-btn-child:hover{
    background: rgba(19, 249, 123, 1) !important;
}
.add-btn{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    margin: 3rem 3rem 0rem 0rem;
}


